//@flow
import * as React from 'react';
import BackDrop from '../BackDrop/BackDrop';
import { t } from '../../../i18n';
import { FormModalContainer } from './FormModal.styles';
type Props = {
  show: boolean,
  handleClose: Function,
  title: string,
  children?: React.Node,
  alert?: () => React.ReactNode,
  handleValidate?: Function,
  validateTitle: string,
  cancelTitle: string,
  hasCancelBtn?: boolean,
  hasValidateBtn?: boolean,
  validateBtnDisabled?: boolean,
  cancelBtnDisabled?: boolean,
};

const FormModalButtons = (props: Props) => (
  <>
    {props.hasCancelBtn && (
      <button
        className="modal-close btn-primary btn"
        data-text="cancel"
        onClick={props.handleClose}
        disabled={props.cancelBtnDisabled}
      >
        <span>{props.cancelTitle}</span>
      </button>
    )}
    {props.hasValidateBtn && (
      <button
        className="modal-close btn-primary btn"
        data-text="save"
        onClick={props.handleValidate}
        disabled={props.validateBtnDisabled}
      >
        <span>{props.validateTitle}</span>
      </button>
    )}
  </>
);
const renderFooter = (props: Props) =>
  props.hasCancelBtn || props.hasValidateBtn;
const FormModal = (props: Props) => (
  <>
    <BackDrop show={props.show} clicked={props.handleClose} />
    <FormModalContainer show={props.show}>
      <div className="modal-content">
        <div className="modal-header">
          <button
            className="modal-close close"
            data-dismiss="modal"
            onClick={props.handleClose}
          >
            Ã—
          </button>
          <h4>{props.title}</h4>
        </div>
        {props.alert && props.alert()}
        <div className="modal-body">{props.children}</div>
        {renderFooter(props) && (
          <div className="modal-footer">
            <p className="align_center"></p>
            <FormModalButtons {...props} />
          </div>
        )}
      </div>
    </FormModalContainer>
  </>
);

FormModal.defaultProps = {
  validateTitle: t('Components:modal.validateBtnTitle'),
  cancelTitle: t('Components:modal.cancelBtnTitle'),
  validateBtnDisabled: false,
  cancelBtnDisabled: false,
};

export default FormModal;
